.exercises-container {
    display: flex;
    position: relative;
    background-color: #000;
    align-items: center;
    justify-content: center;
    padding: 5rem 0;
}

.exercises-container h1 {
    color: #fff;
    text-align: center;
    font-size: 2.5rem;
    letter-spacing: .2rem;
}

.tab-container {
    margin-top: 2rem;
}

.exercises-container ul {
    display: flex;
    align-items: center;
    justify-content: center;
}

.exercises-container ul .bodypart-list {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    color: var(--primary-color);
    padding: 0.5rem 1.2rem;
    margin: 0 0.4rem;
    border-radius: 1.3rem;
    cursor: pointer;
    text-transform: capitalize;
    transition: .3s ease-in-out;
}

.exercises-container ul .bodypart-list:hover {
    background-color: rgb(99, 99, 99);
    color: #fff;
}

.exercises-container .tab-container .active-tab {
    background-color: var(--secondary-color);
    color: var(--primary-color);
}

.tab-container p {
    color: white;
}

.exerciseCard-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 3rem 0;
}

.exercise-card {
    position: relative;
    flex: 0 0 calc(25% - 1rem);
    max-width: calc(25%-1rem);
    width: 100%;
    background-color: #e6e5e5;
    margin: 0.5rem;
    border-radius: 0.5rem;
    transition: .3s ease-in-out;
}


.exercise-card:hover {
    transform: scale(1.05);
}

.exerciseImg-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.exerciseImg-wrapper img {
    height: auto;
    width: 100%;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
}

.exerciseText-wrapper {
    padding: 1rem;
    flex-wrap: wrap;
    border-top: 1px solid #d3d3d3;
}

.exerciseBtn-wrapper {
    display: flex;
}

.exerciseText-wrapper button {
    background-color: var(--primary-color);
    display: block;
    color: #fff;
    border-color: var(--primary-color);
    letter-spacing: 1px;
    margin-right: 0.25rem;
    margin-bottom: 0.7rem;
    font-size: 0.75rem;
    text-transform: capitalize;
}

.exerciseText-wrapper p {
    display: block;
    color: var(--primary-color);
    font-weight: 600;
    margin: 0;
    text-transform: capitalize;
}

.pagination li {
    background-color: #fff;
    margin: 0 0.5rem;
    border-radius: 50%;
}

.pagination li .Mui-selected {
    color: var(--primary-color);
    background-color: var(--secondary-color);
    border-radius: 50%;
}

.pagination .MuiPaginationItem-root:hover {
    color: var(--primary-color);
}