.hero-container {
    display: flex;
    align-items: center;
    position: relative;
    height: 100vh;
    width: 100%;
}

.image-container {
    position: absolute;
    display: flex;
    height: 100vh;
    width: 100%;
}

.hero-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    filter: brightness(0.8);
}

.hero-text {
    position: absolute;
    margin-left: 4rem;
}

.hero-text h2 {
    margin-bottom: 0;
    font-size: 2rem;
    letter-spacing: .1rem;
}

.hero-text h1 {
    font-size: var(--large-font);
    letter-spacing: 0.2rem;
    margin: 0;
}

.hero-text h1 span {
    color: var(--secondary-color);
}

.hero-text h3 {
    color: #bdbd68;
    margin-top: 0.5rem;
    margin-bottom: 2rem;
}

.hero-text button {
    font-size: 1.25rem;
    font-weight: 600;
    padding: 1rem 1.8rem;
    letter-spacing: .1rem;
}