.contact {
    display: flex;
    height: 100vh;
    align-items: center;
}

.contact-text,
.contact-content {
    flex: 0 0 50%;
    max-width: 50%;
    padding: 0 3rem;
}

.contact-text h1 {
    letter-spacing: 0.1rem;
    font-size: 1.8rem;
}

.contact-text p {
    font-size: 1.1rem;
    line-height: 1.7rem;
    margin-bottom: 2rem;
}

.contact-text .contact-details {
    margin-top: 0.3rem;
    vertical-align: middle;
}

.contact-text span {
    letter-spacing: 0.1rem;
    margin-left: 0.2rem;
}

.contact-content h1 {
    font-size: 2rem;
    margin-bottom: 2rem;
}

.contact-form-div {
    position: relative;
    margin-bottom: 1.7rem;
    height: 4rem;
}

.contact-form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #e9e9e9;
    color: var(--primary-color);
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;
}

.contact-form-area {
    height: 11rem;
}

.contact-form-area textarea {
    resize: none;
}

.contact-content button span {
    font-size: 1.1rem;
}

.contact-content button {
    padding: 0.7rem 1.1rem;
}