.trainers-container {
    display: flex;
    height: 100vh;
    align-items: center;
}

.trainers {
    width: 100%;
}

.trainers-container h1 {
    font-size: var(--mid-font);
    letter-spacing: 0.1rem;
    margin-top: 0;
    margin-bottom: 3rem;
    text-align: center;
}

.trainerSlide-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-bottom: 3rem;
}

.slide-container {
    position: relative;
    background-color: #fff;
    color: var(--primary-color);
    border-radius: 0.5rem;
    cursor: pointer;
}

.trainerImg-wrapper {
    height: 250px;
    width: 100%;
    overflow: hidden;
}

.trainerImg-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    transition: 0.5s all ease-in-out;
}

.slide-container:hover img {
    transform: scale(1.2);
}

.trainerText-wrapper {
    padding: 1rem;
}

.trainerText-wrapper h3,
.trainerText-wrapper p {
    margin: 0;
    letter-spacing: 0.1rem;
}

.trainerText-wrapper p {
    margin-bottom: 0.7rem;
}

.trainerText-wrapper ul{
    list-style: none;
    padding-left: 1.5rem;
}

.trainerText-wrapper .trainer-icon {
    margin-right: 0.4rem;
    color: var(--primary-color);
}

.trainerText-wrapper li {
    font-size: 0.9rem;
    letter-spacing: 0.03rem;
}

.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 0.4rem;
}

.swiper-pagination-bullet {
    background-color: #fff !important;
    height: 0.7rem;
    width: 0.7rem;
    opacity: 1;
    transition: 0.3s all ease-in-out;
}

.swiper-pagination-bullet-active {
    background-color: var(--secondary-color) !important;
    height: 0.9rem;
    width: 0.9rem;
}