.package-container {
    display: flex;
    height: 100vh;
    align-items: center;
}

.package {
    width: 100%;
}

.package-container .package-header {
    font-size: var(--mid-font);
    margin: 0;
    text-align: center;
}

.package-container .packages {
    display: flex;
    margin-top: 5.5rem;
}

.packageCard-container {
    flex: 0 0 33.3%;
    max-width: 33.3%;
    width: 100%;
}

.packageCard-container .package-card {
    margin: 0 1.7rem;
    background-color: #fff;
    color: var(--primary-color);
    padding: 1.2rem;
}

.packageCard-container:nth-child(2) .package-card {
    transform: scale(1.16);
}

.packageIcon-wrapper {
    height: 2rem;
    width: 2rem;
    margin-bottom: 1rem;
}

.package-card p {
    text-transform: uppercase;
    font-size: 1.3rem;
    font-weight: 700;
    letter-spacing: 0.1rem;
    margin: 0;
}

.package-card h1 {
    margin: 1rem 0;
    font-size: var(--mid-font);
    margin-bottom: 2rem;
}

.package-card .package-price {
    font-size: 1.3rem;
    font-weight: 500;
}

.package-card ul {
    list-style: none;
    padding-left: 1.5rem;
    margin-bottom: 2rem;
}

.package-card ul li {
    font-size: 1rem;
    line-height: 1.6rem;
    color: #000;
}

.package-card ul .package-icon {
    margin-right: 0.7rem;
}

.packageLink-wrapper {
    margin-bottom: 2rem;
}

.packageLink-wrapper .package-link {
    color: var(--primary-color);
    font-size: 0.85rem;
}

.packageLink-wrapper .packageLink-icon {
    height: 0.7rem;
    width: 0.7rem;
    margin-left: 0.5rem;
}

.package-card button {
    display: block;
    width: 100%;
    border-radius: 0;
    font-size: 1rem;
    font-weight: 600;
    padding: 0.6rem 0;
    color: var(--primary-color);
}