.about {
    display: flex;
    height: 100vh;
    align-items: center;
}

.aboutImg-wrapper {
    flex: 0 0 60%;
    max-width: 60%;
    width: 100%;
    height: 500px;
}

.aboutImg-wrapper img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.aboutText-wrapper {
    flex: 0 0 40%;
    min-width: 40%;
    width: 100%;
    padding: 3rem;
}

.aboutText-wrapper h1 {
    letter-spacing: 0.1rem;
    font-size: var(--mid-font);
}

.aboutText-wrapper p {
    font-size: 1.1rem;
    line-height: 1.6rem;
}