.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: var(--primary-color);
}

.nav-container {
    display: flex;
    align-items: center;
    height: 3.75rem;
}

.header .logo-wrapper {
    display: flex;
    align-items: center;
}

.logo-wrapper .app-logo {
    height: 1.75rem;
    width: 2.5rem;
    color: var(--secondary-color);
    margin-right: .6rem;
}

.header ul {
    display: flex;
    margin-left: 5rem;
}

.header li {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 .3rem;
}

.header a, .header h3 {
    color: #fff;
}

.header ul li a {
    padding: .5rem .9rem;
    border-radius: .4rem;
    transition: .3s ease-in-out;
}

.header ul li a:hover {
    background-color: var(--secondary-color);
    color: var(--primary-color);
}

.header .button-wrapper {
    margin-left: auto;
}

.header .button-wrapper button {
    font-size: 14px;
    letter-spacing: .1rem;
}

.header .button-wrapper button:hover {
    background-color: #000;
}