.footer-container {
    position: relative;
    background-color: var(--primary-color);
    border-top: 1px solid rgb(84, 84, 84);
    bottom: 0;
}

.footer {
    display: flex;
    padding: 4rem 0;
}

.footer h2 {
    margin: 0;
    letter-spacing: 0.1rem;
}

.footer .app-info {
    flex: 0 0 45%;
    max-width: 45%;
    padding-right: 3rem;
}

.footer .app-info a {
    color: #fff;
}

.footer .appInfo-logo {
    display: flex;
}

.footer .appInfo-logo img {
    height: 2rem;
    width: 2.5rem;
    margin-right: 0.5rem;
}

.footer .subscription-form {
    margin-top: 1.3rem;
}

.footer .subscription-form input{
    padding: 0.5rem;
    border-radius: 1rem;
    width: 50%;
    margin-right: 1rem;
    border-color: #fff;
}

.footer .subscription-form button:hover {
    background-color: #fff;
    border-color: #fff;
    color: var(--primary-color);
}

.footer .quick-links {
    flex: 0 0 25%;
    max-width: 25%;
    padding: 0 1rem;
}

.footer .quick-links ul {
    list-style: none;
    padding-left: 0;
}

.footer .quick-links a {
    line-height: 1.6rem;
    color: #fff;
    transition: .1s all ease-in-out;
}

.footer .quick-links a:hover {
    color: var(--secondary-color);
}

.footer .contact-details {
    flex: 0 0 30%;
    max-width: 30%;
}

.footer .contact-details h2,
.footer .app-info h2 {
    margin-bottom: 0.8rem;
}

.footer .contact-details p {
    margin-bottom: 0.2rem;
    font-size: 0.9rem;
}

.footer .social-icons {
    border-top: 1px solid #9f9f9f;
    margin-top: 1.3rem;
    padding-top: 0.7rem;
}

.footer .social-icons a {
    color: #fff;
}

.footer .social-icons .socialMedia-icon {
    height: 1.1rem;
    width: 1.1rem;
}

.footer .social-icons .insta-icon {
    margin-left: 0.9rem;
}

.footer .social-icons .youtube-icon {
    height: 1.4rem;
    width: 1.4rem;
    padding-top: 1px;
}

.footer .social-icons .tiktok-icon {
    margin: 0 0.9rem;
}

.footer p {
    margin: 0;
}

.copyright-container {
    text-align: center;
    padding-bottom: 2rem;
}

.copyright-container .copyright-text {
    font-size: 12px;
    letter-spacing: 0.1rem;
}

.copyright-container h6 {
    font-size: 1rem;
    margin-top: 0.6rem;
    margin-bottom: 0;
}

.copyright-container a {
    color: #fff;
    vertical-align: middle;
}